@import "../../_vars.scss";
@import "../../_mixins.scss";


.footer{
    background: $FooterBg;
    padding: 26px 150px;
    position: absolute;
    bottom: 0;
    height: 73px;
    @include breakpoint(xxxxlarge) {
        padding: 25px 50px;
    }
    @include breakpoint(medium) {
        padding:25px 0 25px 0;
        .ui.doubling.grid{
            .row{
                .column{
                    padding:5px 0 0 0px !important;
                }
            }
        }
    }
  
    .copy{
        color: $FooterText;
        font-size: 14px;
        @include breakpoint(medium) {
            text-align: center;
            padding: 0 20px;
        }
    }
    .footerLink{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        @include breakpoint(medium) {
            justify-content: center;
        }
        @include breakpoint(xsmall) {
            flex-wrap: wrap;
        }
        .item{
            font-size: 14px;
            color: $FooterText !important;
            padding-left: 30px !important;
            @include breakpoint(xsmall) {
                padding-left: 20px !important;
            }
        }
    }
   
}
@include breakpoint(small) {
    .ui.fluid.container.footer{
        margin-left: 0px !important;
        margin-right: 0px !important;
    }
}
body{
    margin-bottom: 73px !important;
    #root{
        height: 100% !important;
    }
}
html {
	position: relative;
	min-height: 100%;
	height: unset !important;
}