.Location{
    position: relative;
    .autocomplete-dropdown-container{
        margin-bottom: 0;
         position: absolute;
        left: 0;
        width: 100%;
        background: #35106a;
        div{
            margin: 0;
            padding: 0;
            border-bottom: 2px solid #2c1f57;
            span{
                padding:8px 15px;
                margin: 0 !important;
                font-size: 23px;
            }
        }
    }
}