@import "../../_vars.scss";
@import "../../_mixins.scss";

.TopHeader {
  position: relative;
  .HeaderTitle {
    position: absolute;
    top: -130px;
    z-index: 0;
    left: 0px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    cursor: pointer;
    h1 {
      font-size: 25px;
      color: $Textwhite;
    }
    a {
      font-size: 15px;
      background: #35106a;
      color: $Textwhite;
      width: 110px;
      font-weight: 600;
      border-radius: 25px;
      text-align: center;
      line-height: 40px;
    }
  }
}
