@import "../../../_vars.scss";
@import "../../../mixins.scss";

.TopBreadcrumb {
  background: #35106a;
  padding: 20px 0;
  .breadcrumb {
    .section {
      font-size: 14px;
      color: $Textwhite !important;
      &.active {
        color: $Textlgtblue !important;
        font-weight: 400 !important;
      }
    }

    .icon {
      color: $Textwhite !important;
    }
  }
}
.MainContainerD {
  padding: 60px 0 80px 0;
  .buyForm {
    padding: 20px;
    .fields {
      .field {
        label {
          color: $Textwhite;
          font-weight: 600;
          margin-bottom: 15px;
          font-size: 15px;
        }
      }
    }
    .inputBox {
      .input {
        border: 1px solid #2c1f57;
        border-radius: 30px;
        height: 54px;
        background: transparent;
        input {
          background: transparent;
          border: 0px;
          font-size: 15px;
          color: $Textlgtblue;
          font-weight: 600;
          &:focus {
            background: transparent;
            border: 0px;
            color: $Textlgtblue;
          }
          &::selection {
            color: $Textlgtblue;
          }
        }
      }
      .selection {
        height: 54px;
        background: transparent;
        border-left: 1px solid #2c1f57;
        display: flex;
        width: 90px;
        align-items: center;
        &:hover {
          border-left: 1px solid #2c1f57;
        }
        .text {
          font-size: 15px;
          color: $Textlgtblue;
          font-weight: 600;
        }
        .icon {
          font-size: 18px;
          color: $Textwhite;
          height: 100%;
          display: flex;
          align-items: center;
        }
      }
    }
    .selectBox {
      .selection {
        height: 54px;
        background: transparent;
        border: 1px solid #2c1f57;
        border-radius: 30px;
        display: flex;
        align-items: center;
        &:hover {
          border: 1px solid #2c1f57;
        }
        .text {
          font-size: 15px;
          color: $Textlgtblue;
          font-weight: 600;
        }
        .icon {
          font-size: 18px;
          color: $Textwhite;
          height: 100%;
          display: flex;
          align-items: center;
        }
      }
    }
    .BtnBox {
      margin-top: 35px !important;
      .button {
        height: 54px;
        border: 0px solid #2c1f57;
        border-radius: 30px;
        background: $buttonBg;
        width: 125px;
        font-weight: 400;
        color: $buttonText;
        font-size: 18px;
        text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.32);
        &:hover {
          background: $buttonBgHover;
          color: $buttonTextHover;
        }
      }
    }
  }
  .tabsBox {
    margin-bottom: 50px;
    .secondary {
      margin-bottom: 0 !important;
      .item {
        color: $tabsColor;
        font-size: 20px;
        font-weight: 400;
        border-radius: 10px 10px 0 0 !important;
        font-family: "Montserrat", sans-serif !important;
        &:hover {
          color: $tabsColorActive !important;
          background: $tabsActiveBg !important;
          font-weight: 400;
        }
        &.active {
          color: $tabsColorActive !important;
          background: $tabsActiveBg !important;
          font-weight: 700;
        }
      }
    }
    .ui.segment {
      margin: 0 !important;
      background: $tabsActiveBg !important;
      border-radius: 10px !important;
      padding: 0 !important;
      table {
        border: 0px !important;
        color: $Textwhite;
        thead {
          background: $BlueHeaderBg;
          tr {
            th {
              color: $Textlgtblue;
              font-size: 14px;
              font-weight: 500;
              font-family: "Montserrat", sans-serif !important;
            }
          }
        }
        tbody {
          background: $BlueBg;
          tr {
            border-bottom: 1px solid #1b043b !important;
            td {
              color: $Textlgtblue;
              font-size: 13px;
              font-weight: 500;
              font-family: "Montserrat", sans-serif !important;
              a {
                color: $Textwhite;
                &.btn {
                  background: #0c0127;
                  color: $Textlgtblue;
                  padding: 5px 20px;
                  border-radius: 15px;
                  display: block;
                  text-align: center;
                  cursor: pointer;

                  &:hover {
                    cursor: pointer;

                    background: #e6a60b;
                    color: $Textwhite;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
.pageBottom {
  margin: 15px 0 30px 0;
  float: right;
  .pagination {
    border: 0px solid #000 !important;
    background: transparent !important;
    .item {
      background: transparent !important;
      border-radius: 50% !important;
      color: #fff !important;
      &:hover,
      &.active {
        background: #e6a60b !important;
      }
    }
  }
}

.tabsBoxSec {
  margin-bottom: 0;
  position: relative !important;
}
.refreshBtn {
  position: absolute;
  right: 0;
  top: 6px;
  height: 36px;
  border: 0px solid #2c1f57;
  border-radius: 30px;
  background: $buttonBg;
  width: 140px;
  padding: 7px 0;
  text-align: center;
  font-weight: 400;
  color: $buttonText;
  font-size: 16px;
  text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.32);
  &:hover {
    background: $buttonBgHover;
    color: $buttonTextHover;
  }
}
@include breakpoint(small) {
  .ui.fluid.container.TopBreadcrumb {
    margin-left: 0px !important;
    margin-right: 0px !important;
  }
}
