
@import "../../../_vars.scss";
@import "../../../mixins.scss";

.sellBoxes{
    background: $BlueBg;
    border-radius: 10px;
    height: 100%;
    .sellBoxesHead{
        border-radius: 10px 10px  0 0;
        padding:27px 34px;
        background: $BlueHeaderBg;
        @include breakpoint(small) {
            padding: 15px 20px;
        }
        h3{
            font-size: 22px;
            font-weight: 700;
            color: $Textwhite;
            @include breakpoint(small) {
                font-size: 18px;
            }
            span{
                color: $Textlgtblue;
            }
        }
    }
    .sellBoxesInfo{
        padding:27px 34px;
        @include breakpoint(small) {
            padding: 10px 20px;
        }
        p{
            font-size: 15px;
            line-height: 29px;
            font-weight: 400;
            color: $Textwhite;
            @include breakpoint(small) {
                font-size: 14px;
                line-height: 20px;
            }
            &.gray{
                color: $Textlgtblue !important;
            }
        }
        .list{
            padding: 10px 0 0px 15px;
            padding-right: 0px;
            .item{
                font-size: 15px;
                line-height: 29px;
                font-weight: 400;
                color: $Textlgtblue;
                list-style: decimal;
                margin-bottom: 10px;
                @include breakpoint(small) {
                    font-size: 14px;
                    line-height: 20px;
                    margin-bottom: 5px;
                }
                &::marker{
                    color: $Textwhite;
                    padding: 15px;
                }
            }
        }
    }
    .sellBoxesForm{
        padding:27px 34px;
        @include breakpoint(small) {
            padding: 15px 20px;
        }
        p{
            font-size: 15px;
            line-height: 21px;
            font-weight: 600;
            color: $Textwhite;
            span{
                color: $Textlgtblue;
            }
        }
        .list{
            padding-left: 0px;
            .item{
                font-size: 15px;
                line-height: 21px;
                font-weight: 600;
                color: $Textlgtblue;
                height: 44px;
                list-style: none;
                @include breakpoint(xsmall) {
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    height: auto;
                }
                &::marker{
                   display: none;
                }
                span{
                    color: $Textwhite;
                    float: right;
                    text-align: right;
                    justify-content: center;
                    @include breakpoint(xsmall) {
                        text-align: left;
                        margin-bottom: 6px;
                   }
                    b{
                        font-size: 13px;
                        font-weight: 400;
                        color: $Textlgtblue;
                        margin-top: -5px;
                        display: block;
                        @include breakpoint(xsmall) {
                            margin-top: 0;
                        }
                    }
                    img{
                        position: relative;
                        top:4px;
                    }
                }
            }
        }
        .sellForm{
            .sellFormTop{
                background: #0f022b;    
                padding: 15px;
                border-radius: 15px;
                .sellFormTopInput{
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 25px;
                    @include breakpoint(xsmall) {
                        flex-direction: column;
                        margin-bottom:0px;
                    }
                    .sellFormInput{
                        width: 45%;
                
                    .input{
                        width: 100%;
                        @include breakpoint(xsmall) {
                            width: 100%;
                            margin-bottom: 20px;
                        }
                    }
                        
                    }
                }
                .field{
                    label{
                        color: $Textwhite;
                        font-weight:600;
                        margin-bottom: 12px;
                        font-size: 15px;
                        line-height: 20px;
                    }
                    .input{
                        border: 1px solid #37396b !important;
                        height: 54px;
                        border-radius:30px;
                        input{
                            background: transparent;
                            font-size: 15px;
                            color: $Textlgtblue;
                            font-weight: 600;
                            &:focus, &:hover, &:active, &:visited{
                                border-color: transparent !important;
                            }           
                        }
                        .label{
                            background: transparent;
                            color: $Textwhite;
                            font-size: 14px;
                            font-weight: 700;
                            position: absolute;
                            text-transform: uppercase;
                            right: 5px;
                            top: 50%;
                            transform: translateY(-50%);
                        }
                    }
                    textarea{
                        height: 120px;
                        border-radius:30px;
                        background: transparent;
                        border: 1px solid #37396b !important;
                        resize: none;
                        color: #fff;
                    }
                }
            }
            .button{
                background: #442461;
                font-size: 19px;
                color: $Textwhite;
                height: 62px;
                border-radius:30px;
                width: 100%; 
                font-weight: 700;
                margin-top: 22px;
            }
        }
    }
}
.MainContainerBuy, .MainContainerSell{
    padding: 60px 0;
    @include breakpoint(small) {
        padding: 30px 0;
    }
}