@import "../../_vars.scss";
@import "../../_mixins.scss";

.headerBg {
  background: url(../../images/headerbg.jpg) repeat;
  height: 280px;
  padding: 25px 150px;
  @include breakpoint(xxxxlarge) {
    padding: 25px 50px;
  }
  @include breakpoint(medium) {
    padding: 15px 0 20px 0;
  }
  .logo {
    background: url(../../images/logo.svg) repeat;
    width: 110px;
    height: 50px;
    display: block;
    background-size: cover;
  }
  .RightMenu {
    display: flex !important;
    justify-content: flex-end;
    align-items: center;
    .HeaderNav {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      .item {
        font-size: 14px;
        color: $NavColor !important;
        padding-left: 30px !important;
        &:hover,
        &.active {
          color: $headerText !important;
          cursor: pointer;
        }
      }
    }
    .userprofileImg {
      width: 46px;
      height: 46px;
      border-radius: 50%;
      margin: -5px 20px 0 20px;
    }
    .userprofile {
      color: $headerText !important;
      margin-top: -5px;
      .text {
        font-size: 15px;
        font-weight: 400;
        font-family: "Ubuntu", sans-serif;
      }
      .icon {
        &::before {
          color: #e6a60b;
        }
      }
    }
  }
}
@include breakpoint(small) {
  .ui.fluid.container.headerBg {
    margin-left: 0px !important;
    margin-right: 0px !important;
  }
}
