@import "../../../_vars.scss";
@import "../../../_mixins.scss";

.TradeBoxOut{
    padding: 55px 0;
    @include breakpoint(small) {
        padding: 20px 0;
    }
    .TradeBox{
        background: $BlueBg;
        border-radius: 10px;
        height: 100%;
        margin-bottom: 47px;
        @include breakpoint(small) {
            margin-bottom: 20px;
        }
        .TradeBoxHead{
            border-radius: 10px 10px  0 0;
            padding:27px 34px;
            background: $BlueHeaderBg;
            @include breakpoint(small) {
                padding: 10px 20px;
            }
            h3{
                font-size: 22px;
                font-weight: 700;
                color: $Textwhite;
                @include breakpoint(small) {
                    font-size: 18px;
                    font-weight: 600;
                }
                span{
                    color: $Textlgtblue;
                }
            }
        }
        .TradeBoxInfo{
            padding:17px 34px;
            @include breakpoint(small) {
                padding: 10px 20px;
            }
            p{
                font-size: 15px;
                line-height: 29px;
                font-weight: 400;
                color: $Textwhite;
                &.gray{
                    color: $Textlgtblue !important;
                }
            }
            .Advertisement{
                padding: 0px;
                .item{
                    font-size: 15px;
                    line-height: 33px;
                    font-weight: 400;
                    padding-left: 34px;
                    color: $Textwhite;
                    list-style: none;
                    position: relative;
                    margin-bottom: 4px;
                    @include breakpoint(small) {
                        font-size: 14px;
                        line-height: 20px;
                        margin-bottom: 5px;
                    }
                    &:before{
                        color: $Textwhite;
                        background: url(../../../images/li_bg.png) no-repeat;
                        position: absolute;
                        background-size:100%; 
                        left: 0; 
                        top:8px;
                        content: "";
                        width: 19px;
                        height: 19px;
                    }
                }
            }
            .CheckList{
                padding: 0px;
                .item{
                    font-size: 15px;
                    line-height: 33px;
                    font-weight: 400;
                    color: $Textwhite;
                    list-style: none;
                    position: relative;
                    margin-bottom: 20px;
                    @include breakpoint(small) {
                        margin-bottom: 5px;
                    }
                    span{
                        font-size: 12px;
                        font-weight: 200;
                        font-style: italic;
                        color: $Textlgtblue;
                        padding-left: 40px;
                        line-height: 18px;
                        display: block;
                        margin-bottom: 5px;
                        a{
                            color: $Textwhite;
                            font-weight: 600;
                        }
                    }
                    .checkbox{
                        label{
                            font-size: 15px;
                            color: $Textwhite;
                            font-weight: 700;
                            padding-left: 40px;
                            @include breakpoint(small) {
                                font-size: 14px;
                                font-weight: 600;
                            }
                            &:before{
                                background: transparent !important;
                                border:1px solid $Textlgtblue; 
                                border-radius: 0;
                            }
                        }
                    }
                }
            }
            h2{
                font-size: 15px;
                font-weight: 600;
                color: #fff;
                line-height: 20px;
                margin-top: 20px;
                span{
                    display: block;
                    font-size: 12px;
                    font-style: italic;
                    color: #5f70aa;
                    margin-top: 1px;
                    font-weight: 400;
                }
            }
            .TradeInput{
                margin-bottom: 20px;
                .TradeRadio{
                    margin-bottom: 20px;
                    .field{
                        width: 50%;
                        display: inline-block;
                        .radio {
                            label{
                                font-size: 15px;
                                font-weight: 400;
                                color: #5f70aa;
                                &:before{
                                    background: transparent !important;
                                    border-color: #3a346c !important;
                                }
                            }
                            &.checked{
                                label{
                                    color: #fff;
                                }
                            }
                        }
                    }
                }
                .field{
                    label{
                        font-size: 15px;
                        color: #fff;
                        font-weight: 600;
                        margin-bottom: 10px;
                    }
                    .input{
                        width: 50%;
                        border: 1px solid #37396b !important;
                        height: 54px;
                        border-radius:30px;
                        input{
                            background: transparent;
                            font-size: 15px;
                            color: $Textlgtblue;
                            font-weight: 600;   
                            &:focus, &:hover, &:active, &:visited{
                                border-color: transparent !important;
                            }                        
                        }
                    }
                    span{
                        display: block;
                        font-size: 12px;
                        font-style: italic;
                        color: #5f70aa;
                        margin-top: 10px;
                    }
                }
            }
            .informationSec{
                .fields{
                    margin-bottom: 22px;
                    justify-content: space-between;
                    @include breakpoint(small) {
                        margin-bottom: 0px;
                    }
                    .field{
                        width: 49%;
                        @include breakpoint(small) {
                            width: 100%;
                            margin-bottom: 20px;
                        }
                        .selection{
                            height: 54px;
                            background: transparent;
                            border: 1px solid #2c1f57;
                            border-radius: 30px;
                            display: flex; 
                            align-items: center;
                            &:hover{
                                border: 1px solid #2c1f57;
                            }
                            .text{
                                font-size: 15px;
                                color: $Textlgtblue;
                                font-weight: 600;
                            }
                            .icon{
                                font-size: 18px;
                                color: $Textwhite;
                                height: 100%;
                                display: flex; 
                                align-items: center;
                            }
                        } 
                        label{
                            font-size: 15px;
                            color: #fff;
                            font-weight: 600;
                            margin-bottom: 10px;
                            span{
                                color: #fff;
                                font-weight: 600;
                                font-style: normal;
                                float: right;
                                margin-top: 0;
                                font-size: 15px;
                                &.spaneq{
                                    color: #5f70aa;
                                }
                            }
                        }
                        .input{
                            width: 100%;
                            border: 1px solid #37396b !important;
                            height: 54px;
                            border-radius:30px;
                            input{
                                background: transparent;
                                font-size: 15px;
                                color: #5f70aa;
                                font-weight: 600;    
                                &:focus, &:hover, &:active, &:visited{
                                    border-color: transparent !important;
                                }                       
                            }
                        }
                        span{
                            display: block;
                            font-size: 12px;
                            font-style: italic;
                            color: #5f70aa;
                            margin-top: 10px;
                        }
                    }

                }
                .field{
                    label{
                        font-size: 15px;
                        color: #fff;
                        font-weight: 600;
                        margin-bottom: 10px;
                    }
                    textarea{
                        background: transparent;
                        font-size: 15px;
                        color: $Textlgtblue;
                        font-weight: 600; 
                        border: 1px solid #37396b !important;
                        height: 54px;
                        border-radius:25px;  
                        resize: none;
                        height: 110px;                        
                    }
                    span{
                        display: block;
                        font-size: 12px;
                        font-style: italic;
                        color: #5f70aa;
                        margin-top: 10px;
                    }
                }
            }
        }
    }
    .button{
        background: #442461 !important;
        font-size: 19px !important;
        color: $Textwhite !important;
        height: 62px !important;
        border-radius:30px !important;
        width: 290px; 
        font-weight: 700 !important;
    }
}
.ui.checkbox input:checked ~ .box::before, .ui.checkbox input:checked ~ label::before{
    border-color: #3a346c !important;
}
.ui.radio.checkbox input:checked ~ .box::after, .ui.radio.checkbox input:checked ~ label::after {
	background-color: #e6a60b !important;
}
.dayTimeList{
    padding-top: 0 !important;
    padding-bottom: 21px !important;
}
.openHours{
    display: block;
    font-size: 12px;
    font-style: italic;
    color: #5f70aa;
    margin-top: 18px;
    b{
        color: #fff;
        font-style: normal;
        font-weight: 600;
        font-size: 15px;
    }
}
.dayTime{
    display: flex;
    margin-bottom: 17px !important;
    .field{
        width: 100% !important;
        justify-content: space-between;
        flex-direction: row;
        display: flex;
        align-items: center;
        label{
            margin-bottom: 0 !important;
        }
        .selection{
            margin: 0 0 0 15px;
        }
    }
}
