@import "../../../_vars.scss";
@import "../../../mixins.scss";

.PaymentSec {
  margin: 55px 0;
  background: $BlueBg;
  padding: 95px 100px 50px 100px;
  border-radius: 10px;
  text-align: center;
  h2 {
    font-size: 40px;
    font-weight: 700;
    color: $Textwhite;
    line-height: normal;
    margin-bottom: 56px;
  }
  h3 {
    font-size: 28px;
    font-weight: 400;
    color: $Textwhite;
    line-height: normal;
    margin-bottom: 5px;
    span {
      font-size: 28px;
      color: #e6a60b;
      font-weight: 600;
    }
  }
  h4 {
    font-size: 21px;
    color: $Textlgtblue;
    font-weight: 300;
    margin-top: 5px;
  }
  .PaymentSecIn {
    background: #0f022b;
    text-align: center;
    padding: 57px 0 60px 0;
    border-radius: 10px;
    h3 {
      font-size: 22px;
      font-weight: 400;
      color: $Textwhite;
      line-height: normal;
      margin-bottom: 43px;
      span {
        font-size: 15px;
        color: $Textlgtblue;
        font-weight: 400;
        margin-top: -5px;
      }
    }
    h4 {
      font-size: 22px;
      color: $Textwhite;
      font-weight: 400;
      text-transform: uppercase;
      margin-top: 5px;
      margin-bottom: 30px;
    }
    .timerBox {
      margin: 0 auto;
      width: 199px;
      height: 199px;
      background: url(../../../images/timer_bg.png) no-repeat;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      h4 {
        font-size: 50px;
        font-weight: 400;
        color: $Textwhite;
        line-height: normal;
        margin-bottom: 0 !important;
      }
      span {
        font-size: 15px;
        color: $Textlgtblue;
        font-weight: 400;
        margin-top: -8px;
      }
    }
  }
  .PaymentSecBot {
    margin-top: 10px !important;
    .PaymentSecLeft {
      text-align-last: left;
      p {
        font-size: 15px;
        color: $Textlgtblue;
        text-transform: uppercase;
        margin-bottom: 8px;
        &.fund {
          color: $Textwhite;
          font-size: 29px;
          line-height: 30px;
          margin-bottom: 0;
        }
      }
      i {
        font-size: 12px;
        color: $Textlgtblue;
        margin-top: -10px;
      }
    }
    .PaymentSecRight {
      .button {
        font-size: 19px;
        font-weight: 700;
        width: 280px;
        color: $Textwhite;
        border-radius: 30px;
        padding: 21px 0;
        background: #442461;
        float: right;
      }
    }
  }
}
.paymentInfo {
  border: 1px solid rgba(255, 255, 2255, 0.5);
  margin: 0 auto;
  width: 450px;
  padding: 20px;
  border-radius: 10px;
  background: #1b043b;
  margin-bottom: 30px;
  .list {
    margin-bottom: 20px !important;
    .item {
      color: #fff;
      text-align: left;
      font-size: 14px;
      margin-bottom: 6px;
      span {
        float: right;
      }
    }
  }
  .field {
    label {
      color: #fff !important;
      text-align: left !important;
      font-weight: 600 !important;
      margin-bottom: 10px !important;
    }
    input {
      background: transparent !important;
      font-size: 15px !important;
      color: #5f70aa !important;
      width: 100%;
      border: 1px solid #37396b !important;
      height: 54px;
      border-radius: 30px !important;
      font-weight: 600 !important;
    }
    .selection {
      height: 54px;
      background: transparent !important;
      border: 1px solid #2c1f57;
      border-radius: 30px !important;
      display: flex;
      border: 1px solid #37396b !important;
      align-items: center;
      &:hover {
        border: 1px solid #2c1f57;
      }
      .text {
        font-size: 15px;
        color: $Textlgtblue;
        font-weight: 600;
        padding-top: 8px;
      }
      .icon {
        font-size: 18px;
        margin-top: 0px !important;
        margin-right: 0px !important;
        color: $Textwhite;
        height: 100%;
        display: flex;
        align-items: center;
      }
    }
  }
}
