
@import "../../../_vars.scss";
@import "../../../_mixins.scss";

.EditModal{
  background: $BlueBg !important;
  .header{
    font-size: 22px;
    font-weight: 700;
    color: $Textwhite !important;
    border-bottom: 0 solid #000;
    border-radius: 10px 10px  0 0;
    background: $BlueHeaderBg !important;
    text-transform: uppercase;
    text-align: center;
  }
  .content{
    padding:17px 34px;
    background: $BlueBg !important;
    .informationSec{
      .addBtn{
        display: flex;
        justify-content: flex-end;
          .button{
            background: #442461;
            font-size: 15px;
            color: $Textwhite;
            height: 52px;
            border-radius:30px;
            width: 250px; 
            font-weight: 700;
            margin-top: 12px;
        }
    }
      .fields{
          margin-bottom: 22px;
          justify-content: space-between;
          .field{
              width: 49%;
              .selection{
                  height: 54px;
                  background: transparent;
                  border: 1px solid #2c1f57;
                  border-radius: 30px;
                  display: flex; 
                  align-items: center;
                  &:hover{
                      border: 1px solid #2c1f57;
                  }
                  .text{
                      font-size: 15px;
                      color: $Textlgtblue;
                      font-weight: 600;
                  }
                  .icon{
                      font-size: 18px;
                      color: $Textwhite;
                      height: 100%;
                      display: flex; 
                      align-items: center;
                  }
              } 
              label{
                  font-size: 15px;
                  color: #fff;
                  font-weight: 600;
                  margin-bottom: 10px;
                  span{
                      color: #fff;
                      font-weight: 600;
                      font-style: normal;
                      float: right;
                      margin-top: 0;
                      font-size: 15px;
                      &.spaneq{
                          color: #5f70aa;
                      }
                  }
              }
              .input{
                  width: 100%;
                  border: 1px solid #37396b !important;
                  height: 54px;
                  border-radius:30px;
                  input{
                      background: transparent;
                      font-size: 15px;
                      color: #5f70aa;
                      font-weight: 600;  
                      &:focus, &:hover, &:active, &:visited{
                        border-color: transparent !important;
                    }                         
                  }
              }
              span{
                  display: block;
                  font-size: 12px;
                  font-style: italic;
                  color: #5f70aa;
                  margin-top: 10px;
              }
          }
        }
      }
  }
}

.AddPaymentBox{
    padding: 55px 0;
    .AddPaymentForm{
      background: $BlueBg;
      border-radius: 10px;
      height: 100%;
      margin-bottom: 47px;
      .AddPaymentFormHead{
          border-radius: 10px 10px  0 0;
          padding:27px 34px;
          background: $BlueHeaderBg;
          @include breakpoint(small) {
            padding: 10px 20px;
           }
          h3{
              font-size: 22px;
              font-weight: 700;
              color: $Textwhite;
              @include breakpoint(small) {
                font-size: 18px;
                font-weight: 600;
              }
              span{
                  color: $Textlgtblue;
              }
          }
      }
      .AddPaymentInfo{
        padding:17px 34px;
        .informationSec{
          .addBtn{
            display: flex;
            justify-content: flex-end;
              .button{
                background: #442461;
                font-size: 19px;
                color: $Textwhite;
                height: 52px;
                border-radius:30px;
                width: 200px; 
                font-weight: 700;
                margin-top: 12px;
            }
        }
          .fields{
              margin-bottom: 22px;
              justify-content: space-between;
              @include breakpoint(small) {
                  flex-direction: column;
                  margin-bottom: 0;
              }
              .field{
                  width: 49%;
                  @include breakpoint(small) {
                        width: 100%;
                        margin-bottom: 20px;
                    }
                  .selection{
                      height: 54px;
                      background: transparent;
                      border: 1px solid #2c1f57;
                      border-radius: 30px;
                      display: flex; 
                      align-items: center;
                      &:hover{
                          border: 1px solid #2c1f57;
                      }
                      .text{
                          font-size: 15px;
                          color: $Textlgtblue;
                          font-weight: 600;
                      }
                      .icon{
                          font-size: 18px;
                          color: $Textwhite;
                          height: 100%;
                          display: flex; 
                          align-items: center;
                      }
                  } 
                  label{
                      font-size: 15px;
                      color: #fff;
                      font-weight: 600;
                      margin-bottom: 10px;
                      span{
                          color: #fff;
                          font-weight: 600;
                          font-style: normal;
                          float: right;
                          margin-top: 0;
                          font-size: 15px;
                          &.spaneq{
                              color: #5f70aa;
                          }
                      }
                  }
                  .input{
                      width: 100%;
                      border: 1px solid #37396b !important;
                      height: 54px;
                      border-radius:30px;
                      input{
                          background: transparent;
                          font-size: 15px;
                          color: #5f70aa;
                          font-weight: 600;  
                          &:focus, &:hover, &:active, &:visited{
                            border-color: transparent !important;
                        }                         
                      }
                  }
                  span{
                      display: block;
                      font-size: 12px;
                      font-style: italic;
                      color: #5f70aa;
                      margin-top: 10px;
                  }
              }

          }
          .field{
              label{
                  font-size: 15px;
                  color: #fff;
                  font-weight: 600;
                  margin-bottom: 10px;
              }
              textarea{
                  background: transparent;
                  font-size: 15px;
                  color: $Textlgtblue;
                  font-weight: 600; 
                  border: 1px solid #37396b !important;
                  height: 54px;
                  border-radius:25px;  
                  resize: none;
                  height: 110px;                        
              }
              span{
                  display: block;
                  font-size: 12px;
                  font-style: italic;
                  color: #5f70aa;
                  margin-top: 10px;
              }
          }
      }
    }
  }
}

.paymentList{
    table {
        border: 0px !important;
        color:$Textwhite;
        thead {
          background: $BlueHeaderBg;
          tr {
            th {
              color: #5f70aa;
              font-size: 14px;
              font-weight: 500;
              font-family: "Montserrat", sans-serif !important;
            }
          }
        }
        tbody {
          background: $BlueBg;
          tr {
            border-bottom: 1px solid #1b043b !important;
            td {
              color: #5f70aa;
              font-size: 13px;
              font-weight: 500;
              font-family: "Montserrat", sans-serif !important;
              a {
                color:$Textwhite;
                &.btn {
                  background: #0c0127;
                  color: #5f70aa;
                  padding: 8px;
                  border-radius: 10px;
                  text-align: center;
                  cursor: pointer;
                  .icon{
                      margin: 0;
                  }
                  &:hover {
                    background: #e6a60b;
                    color:$Textwhite;
                    cursor: pointer;
                  }
                }
              }
            }
          }
        }
      }
}