@import "../../../_vars.scss";
@import "../../../_mixins.scss";


.MainContainer {
  padding: 60px 0;
  .buyForm {
    padding: 20px;
    .fields {
      .field {
        label {
          color:$Textwhite;
          font-weight: 600;
          margin-bottom: 15px;
          font-size: 15px;
        }
      }
    }
    .inputBox {
      .input {
        border: 1px solid #2c1f57;
        border-radius: 30px;
        height: 54px;
        background: transparent;
        input {
          background: transparent;
          border: 0px;
          font-size: 15px;
          color: #5f70aa;
          font-weight: 600;
          &:focus {
            background: transparent;
            border: 0px;
            color: #5f70aa;
          }
          &::selection {
            color: #5f70aa;
          }
        }
      }
      .selection {
        height: 54px;
        background: transparent;
        border-left: 1px solid #2c1f57;
        display: flex;
        width: 90px;
        align-items: center;
        &:hover {
          border-left: 1px solid #2c1f57;
        }
        .text {
          font-size: 15px;
          color: #5f70aa;
          font-weight: 600;
        }
        .icon {
          font-size: 18px;
          color: $Textwhite;
          height: 100%;
          display: flex;
          align-items: center;
        }
      }
    }
    .selectBox {
      .selection {
        height: 54px;
        background: transparent;
        border: 1px solid #2c1f57;
        border-radius: 30px;
        display: flex;
        align-items: center;
        &:hover {
          border: 1px solid #2c1f57;
        }
        .text {
          font-size: 15px;
          color: #5f70aa;
          font-weight: 600;
        }
        .icon {
          font-size: 18px;
          color: $Textwhite;
          height: 100%;
          display: flex;
          align-items: center;
        }
      }
    }
    .BtnBox {
      margin-top: 35px !important;
      .button {
        height: 54px;
        border: 0px solid #2c1f57;
        border-radius: 30px;
        background: #e6a60b;
        width: 125px;
        font-weight: 400;
        color: #fdfdfd;
        font-size: 18px;
        text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.32);
      }
    }
  }
  .tabsBox {
    margin-bottom: 50px;
    .menu {
      margin-bottom: 0 !important;
      .item {
        color: $tabsColor;
        font-size: 20px;
        font-weight: 400;
        border-radius: 10px 10px 0 0 !important;
        font-family: 'Montserrat', sans-serif !important;
        &:hover {
          color: $tabsColorActive !important;
          background: $tabsActiveBg !important;
          font-weight: 700;
        }
        &.active {
          color: $tabsColorActive !important;
          background: $tabsActiveBg !important;
          font-weight: 700;
          
        }
      }
    }
    .ui.segment {
      margin: 0 !important;
      background: $tabsActiveBg !important;
      border-radius: 10px !important;
      padding: 0 !important;
      table {
        border: 0px !important;
        color:$Textwhite;
        thead {
          background: $BlueHeaderBg;
          tr {
            th {
              color: #5f70aa;
              font-size: 14px;
              font-weight: 500;
              font-family: "Montserrat", sans-serif !important;
            }
          }
        }
        tbody {
          background: $BlueBg;
          tr {
            border-bottom: 1px solid #1b043b !important;
            td {
              color: #5f70aa;
              font-size: 13px;
              font-weight: 500;
              font-family: "Montserrat", sans-serif !important;
              a {
                color:$Textwhite;
                &.btn {
                  background: #0c0127;
                  color: #5f70aa;
                  padding: 5px 20px;
                  border-radius: 15px;
                  display: block;
                  text-align: center;
                  cursor: pointer;
                  &:hover {
                    background: #e6a60b;
                    color:$Textwhite;
                    cursor: pointer;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
.infoCyberninza {
  padding-bottom: 0;
  .infoCyberHead {
      background: $headingBG;
      border-radius: 10px 10px  0 0;
      padding:27px 34px;
      color: $Textwhite;
      font-size: 22px;
      margin-bottom: 0;
      @include breakpoint(small) {
        padding: 10px 20px;
        font-size: 18px;
        font-weight: 600;
    }
  }
  .infoCyberBody {
    padding: 40px 34px 34px;
    background: $tabsActiveBg;
    border-radius: 0px 0px 10px 10px;
    @include breakpoint(medium) {
      padding: 20px 15px;
    }
    @include breakpoint(xxsmall) {
      text-align: center;
    }
    .reportUser {
      color: $Textlgtblue;
      font-size: 15px;
      font-weight: 600;
      margin-top: 10px;
    }
    h3 {
      color: $Textwhite;
      font-size: 22px;
      margin: 0;
      padding: 6px 0 27px;
      @include breakpoint(medium) {
        padding: 6px 0 7px;
      }
    }
    button {
      background: #442461;
      border: none;
      border-radius: 100px;
      width: 45%;
      padding: 16px 15px;
      color: $Textwhite;
      font-size: 19px;
      font-weight: 700;
      text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.32);
      @include breakpoint(medium) {
        width: 100%;
      }
      img.ui.image {
        display: inline-block;
        margin-right: 8px;
      }
    }
    .topSec {
      display: flex;
      justify-content: space-between;
      padding-bottom: 60px;
      @include breakpoint(medium) {
        flex-wrap: wrap;
        padding-bottom: 25px;
      }
      .list{
        padding-left: 0px;
        .item{
            font-size: 15px;
            line-height: 21px;
            font-weight: 600;
            color: #5f70aa;
            height: 44px;
            list-style: none;
            &::marker{
               display: none;
            }
            span{
                color:$Textwhite;
                float: right;
                text-align: right;
                b{
                    font-size: 13px;
                    font-weight: 400;
                    color: #5f70aa;
                    margin-top: -5px;
                    display: block;
                }
                img{
                    position: relative;
                    top:4px;
                }
                &.verify {
                    position: relative;
                    &:before {
                      content: "";
                      background: url(../../../images/li_bg.png) no-repeat;
                      height: 14px;
                      width: 14px;
                      background-size: contain;
                      background-position: center center;
                      top: 4px;
                      position: absolute;
                      left: -22px;
                      display: inline-block;
                    }
                  }
            }
        }
    }
      .blkComn {
        flex: 0 0 45%;
        @include breakpoint(medium) {
          flex: 0 0 100%;
        }
      }
    }
  }
}
